.modal__overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  transition: visibility 0.5s, opacity 0.5s linear;
}

.modal__overlay.modal__overlay_open {
  opacity: 1;
  visibility: visible;
  display: flex;
}

.modal {
  background: white;
  padding: 20px 30px;
  border-radius: 10px;
  text-align: center;
  max-width: 400px;
  width: 100%;
  position: relative;
}

.modal__buttons {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  gap: 15px;
}

.modal__button {
  padding: 8px 16px;
  cursor: pointer;
  font-weight: 600;
  border: none;
  border-radius: 6px;
}

.modal__button-confirm {
  background: #ee3465;
  color: white;
}

.modal__button-cancel {
  background: black;
  color: white;
}

.modal__title {
  color: black;
}

.modal__button-close {
  position: absolute;
  right: -40px;
  top: -40px;
  border: none;
  background: transparent;
  background-image: url(../../images/close.svg);
  width: 32px;
  height: 32px;
  background-repeat: no-repeat;
}

@media screen and (max-width: 767px) {
  .modal__button-close{
    width: 20px;
    height: 20px;
    right: 10px;
    top: -36px;
    background-size: contain;
    background-repeat: no-repeat;
  }
}

.modal__button-close:hover {
  opacity: 0.8;
  transition: opacity 0.2s linear;
  cursor: pointer;
}
